<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0" v-if="store">جدول مخزن ({{store.name}})</h4>
                      <form-modal ref="FormModal"/>
                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div class="card-body">
                          <div class="card-title mb-5">
                            <div class="form-inline float-md-end">
                                <div class="search-box ms-2">
                                    <div class="position-relative">
                                        <input type="text" v-model="filter.product" @change="fetchData" class="form-control bg-light rounded" placeholder="Search...">
                                        <i class="mdi mdi-magnify search-icon"></i>
                                    </div>
                                </div> 
                            </div>
                          </div>
                          <div v-if="order_products&&order_products.data.length">
                            <div class="table-responsive" style="overflow-x: inherit;">
                                <table class="table table-nowrap table-centered mb-0">
                                    <thead>
                                        <tr>
                                            <th>المنتج</th>
                                            <th>المورد</th>
                                            <th>الكمية</th>
                                            <th>سعر بيع</th>
                                            <th>سعر شراء</th>
                                            <th>تاريخ الوارد</th>
                                            <th>خيارات</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(order_product, index) in order_products.data">
                                            <td>
                                              <i class="far fa-check-circle text-success"></i>
                                              <label class="fw-bold text-dark mb-0">{{appendParentName(order_product.product)}}</label>
                                            </td>
                                            <td>{{(order_product.order.supplier)?order_product.order.supplier.name:'Admin'}}</td>
                                            <td>{{order_product.avaliable}}</td>
                                            <td>{{moneyFormat(order_product.product.price,0)}}</td>
                                            <td>{{moneyFormat(order_product.price,0)}}</td>
                                            <td>{{order_product.order.created_at| moment("DD-MM-YYYY")}}</td>
                                            <td>
                                              <div class="btn-group">
                                                  <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                    المزيد <i class="mdi mdi-chevron-down"></i>
                                                  </button>
                                                  <div class="dropdown-menu" style="min-width: 120px;">
                                                      <router-link :to="{ name: 'supplier_orders_show', params: { order_id: order_product.order.id } }" tag="a" class="dropdown-item">
                                                          <i class="far fa-eye"></i> تفاصيل الفاتورة
                                                      </router-link>
                                                      <a v-if="order_product.details.length" @click="$refs.FormModal.show(order_product.details)" class="dropdown-item dropdownItem" href="javascript:;">
                                                        <i class="far fa-eye"></i> تفاصيل المنتج
                                                      </a>
                                                  </div>
                                              </div>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                            <pagination-nav class="mt-3" v-if="order_products.last_page>1" :pageSize="order_products.last_page" :currentPage="order_products.current_page" :GoToPage="fetchData" />
                          </div>
                          <div v-else >
                            <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                            <p v-else class="card-title-desc" style="text-align: center;">
                              لا يوجد منتجات في المخزن
                            </p>
                          </div>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
         
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import FormModal from '../../components/modals/store/show';
import PaginationNav from '../../components/pagination';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
      return {
        order_products:null,
        store:false,
        loader:false,
        filter:{
          product:null,store_id:null
        }
      }
    },  
  created() {
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.order_products=null;
      this.loader=true;
      this.filter.store_id=this.$route.params.store_id
      let data = {params: {page:num,filter:this.filter}};
      axios.get('/api/stores/'+this.$route.params.store_id,data).then(({data}) => {
          this.order_products=data.success.order_products;
          this.store=data.success.store;
          if(!data.success.order_products.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>